#container {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

#top_div {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 50%;
  text-align: center;
}

.bottom_div {
  position: absolute;
  top: 50%;
  left: 0;
  right:0;
  bottom: 0;
}

.question {
  font-family: "Poppins", sans-serif;
  font-size: clamp(20px, 2vw, 40px);
  font-weight: 400;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  text-align: center;
  transform: translate(-50%, -50%);
  color:#444452;
}
